.selectFootBox {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0rem;
  left: 0rem;
  bottom: 0rem;
  right: 0rem; }
  .selectFootBox .bg {
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0.7;
    position: absolute;
    z-index: -1; }
  .selectFootBox .content {
    height: 4.5rem;
    position: absolute;
    background: #ffffff;
    z-index: 0;
    bottom: 0rem;
    left: 0rem;
    right: 0rem; }
  .selectFootBox header {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    padding: 0.2rem;
    box-sizing: border-box;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #999999;
    margin-bottom: 0.5rem; }
    .selectFootBox header span:last-child {
      color: #3089DC; }
  .selectFootBox .cityUlBox {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    position: absolute;
    top: 1rem;
    padding-top: 1.4rem;
    overflow: hidden; }
  .selectFootBox ul {
    width: 33%;
    position: relative;
    padding: 0;
    margin: 0;
    top: 0rem;
    -webkit-transition: all 0.6s ease 0s;
    transition: all 0.6s ease 0s; }
  .selectFootBox li {
    width: 100%;
    text-align: center;
    height: 0.7rem;
    line-height: 0.7rem;
    color: #333333;
    padding: 0;
    list-style: none; }
  .selectFootBox .currentSelect {
    position: absolute;
    top: 2.4rem;
    width: 100%;
    height: 0.7rem;
    border-top: 0.01rem solid #E5E5E5;
    border-bottom: 0.01rem solid #E5E5E5; }

